@import "../Header/Header.module.scss";
.Profile {
  display: flex;
  width: 80vw;
  height: 100vh;
  align-items: center;
  justify-content: center;

  .ProfilePic {
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
      font-family: $font-for-text;
      font-size: 40px;
      font-weight: 600;
    }

    img {
      border-radius: 100%;
      height: 300px;
      width: 300px;
      object-fit: fill;
      border: none;
      margin: 10px;
      background-color: white;
    }
  }

  label,
  button {
    background-color: darkgray;
    color: black;
    font-family: $font-for-text;
    font-size: 15px;
    border: none;
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
  }

  label:hover,
  button:hover {
    background-color: rgb(56, 56, 56);
    color: white;
  }

  .UserInfo {
    font-family: $font-for-text;

    h2 {
      margin: 10px;
      color: white;
    }

    .UserDescription {
      display: flex;
      margin: 10px;
      input {
        background-color: rgb(56, 56, 56);
        border-radius: 10px;
        font-family: $font-for-text;
        font-size: 20px;
        color: white;
        padding: 0 10px;
        border: none;
      }
    }
  }
}
